   font-size: inherit;
   line-height: inherit;
   margin: 0;
   block-size: 2.4rem;
   inline-size: 2.4rem;
   vertical-align: sub;
}

form .radio-group label {
   margin: 0.3rem;
}

form input[type="checkbox"],
form input[type="radio"] {
   position: relative;
   appearance: none;
   width: 1.4em;
   height: 1.4em;
   border-radius: 3px;
   margin-right: 0.8vmin;
   background: var(--bg);
   box-shadow: inset 1px 1px 7px rgba(0, 0, 0, 0.75);
}
/* チェックされたらラジオボタン内側を表示 */
form input[type="checkbox"]:checked::before,
form input[type="radio"]:checked::before {
   opacity: 1;
}
form input[type="checkbox"]::before,
form input[type="radio"]::before {
   content: "";
   background-color: var(--text);
   box-shadow: 0 1px 2px 0px rgba(46, 46, 46, 0.45);
   border-radius: 3px;
   margin: auto;
   opacity: 0;
   position: absolute;
   top: 0.14em;
   right: 0;
   bottom: 0;
   left: 0;
   width: 0.7em;
   height: 0.7em;
}

.radio-group label {
   font-size: 1.8rem;
}

.form-row {
   display: flex;
   justify-content: space-between;
}

.col {
   /*float: left;*/
   flex: 1;
   font-weight: 600;
}

.col.x-50 {
   padding: 0 1.5rem 0;
   margin-top: 1.5rem;
}

.col.x-100 {
   padding-right: 1.5rem;
   margin-top: 1.5rem;
}

