}*/

input:-webkit-autofill,
input:-webkit-autofill:focus {
   transition: background-color 0s 600000s, color 0s 600000s !important;
}

.contact-form {
   width: calc(60% - 145px);
   position: absolute;
   top: 25%;
   right: 17.5%;
}

.contact-form span {
   font-size: 1.2rem;
   color: var(--accent);
   display: contents;
}

.get-in-touch {
   margin: 0 auto;
   position: relative;
}

.form-field {
   position: relative;
}

.contact-form .input-text {
   font-size: 2rem;
   font-family: sans-serif;
   font-weight: 400;
   position: relative;
   color: var(--text);
   display: block;
   padding-left: 2rem;
   width: 270px;
   height: 75px;
   border-width: 0;
   background-color: var(--bg);
   box-shadow: inset 1px 1px 5px rgba(0, 0, 0, 0.95);
}

.contact-form #message {
   width: 100%;
   height: 251.3px;
   margin: 0 auto;
   padding-top: 1rem;
}

.input-text:focus {
   outline: none;
}

.input-text:focus + .label,
.input-text.not-empty + .label {
  transform: translateY(-2rem);
  font-size: 1.2rem;
}

.contact-form .label {
   font-family: sans-serif;
   font-size: clamp(1.6rem, 1.181rem + 0.52vw, 1.8rem);
   font-weight: 400;
   color: rgba(10, 10, 10, 0.65);
   /*-webkit-text-stroke: 0.5px rgba(50, 50, 50, 0.5);*/
   position: absolute;
   left: 1.4em;/*rem？*/
   top: 0.5rem;
   pointer-events: none;
   transition:
      transform 0.2s ease-in-out,
      font-size 0.2s ease-in-out;
}

.contact-form .submit-btn {
   font-family: sans-serif;
   font-size: clamp(1.8rem, 1.467rem + 0.44vw, 2rem);
   width: 42.5%;
   height: 35pt;
   display: inline-block;
   color: var(--bg);
   background-color: rgba(65, 65, 65, 0.5);
   text-transform: uppercase;
   letter-spacing: 0.2rem;
   line-height: 1.5rem;
   padding-top: 0.5rem;
   margin-top: 1.5rem;
   margin-bottom: 1.5rem;
   border: none;
   box-shadow: inset 0px 0px 5.5px rgba(0, 0, 0, 0.95);
   cursor: pointer;
   transition: all 0.35s ease;
}

.contact-form .submit-btn:hover {
   color: var(--text);
   background: rgba(160, 113, 113, 0.6);
   border: none;
}

.align-center {
   font-size: clamp(1rem, 0.667rem + 0.44vw, 1.2rem);
   font-family: sans-serif;
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0 1.6rem;
   gap: 1.75em;
   line-height: 1;
}

/*===================== モーダルウィンドウ =====================*/
/*未*/
form .modal {
   display: none;
   position: fixed;
   z-index: 1000;
   top: 0;
   width: 100%;
   height: 100%;
   overflow: auto;
   margin-top: 25vmin;
}

