      height: 30pt;
      padding: 0;
      margin: 0;
      margin-bottom: 0.5rem;
      margin-top: 1rem;
   }

   .contact-form .submit-btn:active {
      color: var(--text);
      background: var(--accent);
   }

   .align-center {
      display: grid;
      align-items: center;
      justify-content: flex-start;
   }

   .align-center p {
      font-size: clamp(1rem, 0.771rem + 1.14vw, 1.2rem);
